import React, { useState } from 'react';
import { Slider, Box, Typography, AppBar, Toolbar, IconButton, Paper } from '@mui/material';
import ResumeIcon from '@mui/icons-material/Description';
import SocialLinks from './SocialLinks'; // Replace with actual import path
import { levelMessages } from './DataComponent'; // Replace with actual import path
import '@fontsource/kalam';

const Portfolio = () => {
  const [sliderValue, setSliderValue] = useState(4);

  const handleSliderChange = (event, newValue) => {
    setSliderValue(newValue);
  };

  const sliderMarks = [
    { value: 0, label: 'Low Hard Sell' },
    { value: 9, label: 'High Hard Sell' }
  ];

  return (
    <Box sx={{ backgroundColor: 'black', color: 'white', minHeight: '100vh', display: 'flex', flexDirection: 'column' }}>
      {/* Navigation Bar */}
      <AppBar position="sticky" sx={{ borderBottom:'1px solid gray', backgroundColor: 'black' }}>
        <Toolbar sx={{ display: 'flex', justifyContent: 'space-between' }}>
          <Typography sx={{ fontFamily: `kalam`, fontWeight: 'bold', color: 'white', fontSize:'1.2rem' }}>
            Arjun Sampath Kumar
          </Typography>
          <a href="https://arjun.guru/resume" target="_blank" rel="noopener noreferrer">
            <IconButton edge="end" color="white">
                <Typography sx={{ fontSize:'1.2rem', fontFamily: `kalam`, fontWeight: 'bold', color: 'white' }}>
                    Resume
                </Typography>
                <ResumeIcon sx={{color:'white'}} />
            </IconButton>
          </a>
        </Toolbar>
      </AppBar>

      {/* Main Content */}
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'center',
          flexGrow: 1,
          padding: '2rem',
        }}
      >
        {/* Dynamic Content */}
        <Paper
          sx={{
            padding: '2rem',
            backgroundColor: 'transparent',
            borderRadius: '15px',
            textAlign: 'center',
            maxWidth: '90vw',
            marginBottom: '5vh',
          }}
        >
          <Typography
            variant="body1"
            sx={{
              fontSize: 'calc(1rem + 1.5vw)',
              fontWeight: 'bold',
              fontFamily: `kalam`,
              '& a': {
                color: 'white',
                },
              color: 'white',
            }}
            dangerouslySetInnerHTML={{ __html: levelMessages[sliderValue] }}
          />
        </Paper>

        {/* Slider */}
        <Slider
          aria-label="Interactive slider"
          value={sliderValue}
          onChange={handleSliderChange}
          step={1}
          marks={sliderMarks}
          min={0}
          max={9}
          sx={{
            width: '80%',
            maxWidth: '80vw',
            color: 'white',
            '& .MuiSlider-thumb': {
              backgroundColor: 'white',
              '&:focus, &:hover, &.Mui-active': {
                boxShadow: '0 0 0 8px rgba(255, 255, 255, 0.3)',
              },
            },
            '& .MuiSlider-rail, & .MuiSlider-track': {
              height: 3,
              backgroundColor: 'gray',
            },
            '& .MuiSlider-markLabel': {
              color: 'white',
              fontSize: '1rem',
              fontFamily: `kalam`,            
            },
          }}
        />
      </Box>

      <SocialLinks/>
    </Box>
  );
};

export default Portfolio;
