import React from 'react';
import { IconButton } from '@mui/material';
import { GitHub, LinkedIn, MailOutline, Newspaper, Twitter, Telegram } from '@mui/icons-material';

const SocialLinks = () => {
  return (
    <div style={{ display: 'flex', justifyContent: 'center', background:'white'}}>
      <IconButton href="https://github.com/admiralarjun" target="_blank" title="GitHub">
        <GitHub fontSize="large" />
      </IconButton>
      <IconButton href="https://x.com/admiralarjun" target="_blank" title="Twitter (X)">
        <Twitter fontSize="large" />
      </IconButton>
      <IconButton href="https://t.me/admiralarjun" target="_blank" title="Telegram">
        <Telegram fontSize="large" />
      </IconButton>
      <IconButton href="https://linkedin.com/in/admiralarjun" target="_blank" title="LinkedIn">
        <LinkedIn fontSize="large" />
      </IconButton>
      <IconButton href="https://substack.com/@admiralarjun" target="_blank" title="Newsletter">
        <Newspaper fontSize="large" />
      </IconButton>
      <IconButton href="mailto:0xArjun@gmail.com" target="_blank" title="Email">
        <MailOutline fontSize="large" />
      </IconButton>
    </div>
  );
};

export default SocialLinks;
